.mainMenu {
    background: #ffa96c;
    height: 40px;
    min-height: 36px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.mainMenu a {
    padding: 0 12px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 14px;
    height: 30px;
    border-radius: 4px;
    font-weight: bold;
    margin: 0 2px;
}

.mainMenu a:hover {
    background: rgba(255, 252, 230, 0.73);
}

.active {
    color: #b700f3;
    background: #fff5af;
}

.menuGroup {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
}

.addBtn {
    color: #fff;
    background: #8080ff;
    margin-right: 12px;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
    position: relative;
    background: #00aaff;
    width: 30px;
    height: 30px;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}

.addBtn > div {
    width:100%;
    height:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    -webkit-animation: rotateKey 3s linear infinite;

            animation: rotateKey 3s linear infinite;
    -webkit-animation-play-state:paused;
            animation-play-state:paused;
    /*transform: translate3d(0,0,0);*/
}

.addBtn > div > div {
    position: absolute;
    width: 18px;
    height: 2px;
    border-radius: 4px;
    background: #fff;
}

.addBtn > div > div:first-child {
    width: 18px;
    height: 2px;
}

.addBtn > div > div:first-child {
    width: 2px;
    height: 18px;
    /*transform: rotate(90deg);*/
}

.addBtn:hover {
    background: #07f;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
    cursor: pointer;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.addBtn:hover > div {
    /*transform: translate3d(0,0,0);*/
    -webkit-animation-play-state:running;
            animation-play-state:running;
}

@-webkit-keyframes rotateKey {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }

}

@keyframes rotateKey {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }

}



