.aboutContainer {
    margin: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    overflow: hidden;
}

.aboutContainer > div:nth-of-type(2) {
    background: #def;
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 10px;
}

.aboutContent {
    overflow: auto;
}

@media all and (max-width: 320px) {

    .aboutContainer {
        margin: 0px;
        padding: 0px;
        border: none;
        border-bottom: 1px solid #ccc;
    }
}



