.aboutMenu {
    background: #dfd;
    -ms-flex-negative:0;
        flex-shrink:0;
    -ms-flex-preferred-size: 140px;
        flex-basis: 140px;
    /*overflow: hidden;*/
    overflow: auto;
}

.aboutMenu a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 10px;
    height: 30px;
}

.aboutMenu a:hover {
    color: #0a61ff;
    background: #9effaa;
    cursor: pointer;
}

.aboutMenu a.active {
    color: #0a61ff;
    background: #9effaa;
}



